* {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.images .active {
  opacity: 1;
  transition: .2s ease-in-out;
}

.noActive {
  opacity: 0;
  transition: .5s ease-in-out;
}

.result {
  margin-left: 5px;
  margin-right: 5px;
  padding-top: 20px;
}

.result h3, .result h4 {
  font-weight: normal;
}
.result em {
  font-weight: bold;
}

.allData {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
}

.icon {
  font-size: 100px;
  display: block;
  margin: 0 auto;
  text-align: center;
  color: white;
}

.data h3, .data h4 {
  color: white;
  text-align: center;
  font-size: 24px;
}

.images img{
  position: absolute;
  width: 100vw;
  height: 100%;
  opacity: 0;
  top: 0;
  left: 0;
  overflow: hidden;
}

@media only screen and (max-width: 600px) {
  .data h3, .data h4 {
    color: white;
    text-align: center;
    font-size: 16px;
  }

  .images {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
  }
}