body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.inputForm {
    display: block;
    margin: 0 auto;
    position: absolute;
    z-index: 10;
    top: 5%;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    margin: 0;
    padding: 0;
}
* {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.images .active {
  opacity: 1;
  transition: .2s ease-in-out;
}

.noActive {
  opacity: 0;
  transition: .5s ease-in-out;
}

.result {
  margin-left: 5px;
  margin-right: 5px;
  padding-top: 20px;
}

.result h3, .result h4 {
  font-weight: normal;
}
.result em {
  font-weight: bold;
}

.allData {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 100;
}

.icon {
  font-size: 100px;
  display: block;
  margin: 0 auto;
  text-align: center;
  color: white;
}

.data h3, .data h4 {
  color: white;
  text-align: center;
  font-size: 24px;
}

.images img{
  position: absolute;
  width: 100vw;
  height: 100%;
  opacity: 0;
  top: 0;
  left: 0;
  overflow: hidden;
}

@media only screen and (max-width: 600px) {
  .data h3, .data h4 {
    color: white;
    text-align: center;
    font-size: 16px;
  }

  .images {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
  }
}
.App {
  padding: 0;
  margin: 0;
  min-width: 100vw;
  height: 100vh;
  background-image: url(/weatherapp/static/media/background.ba4be204.png);
  background-size: cover;
  background-position: center;
}

.App input, .App button {
  padding: 10px 20px;
  background-color: transparent;
  color: white;
  border: 3px solid white;
  transition: .3s;
}

input::-webkit-input-placeholder{
  text-align: center;
  color: white;
}

input::-ms-input-placeholder{
  text-align: center;
  color: white;
}

input::placeholder{
  text-align: center;
  color: white;
}

input[type=text] 
{ text-align:center }

.App button {
  cursor: pointer;
}
.App button:hover {
  background-color: #000;
  color: white;
  border-color: black;
}
