.App {
  padding: 0;
  margin: 0;
  min-width: 100vw;
  height: 100vh;
  background-image: url("./images/background.png");
  background-size: cover;
  background-position: center;
}

.App input, .App button {
  padding: 10px 20px;
  background-color: transparent;
  color: white;
  border: 3px solid white;
  transition: .3s;
}

input::placeholder{
  text-align: center;
  color: white;
}

input[type=text] 
{ text-align:center }

.App button {
  cursor: pointer;
}
.App button:hover {
  background-color: #000;
  color: white;
  border-color: black;
}